<h2>属性</h2>
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>show</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> 指定 path 是否显示。</td>
</tr>
<tr>
<td>leadTime</td>
<td>Number</td>
<td></td>
<td><code v-pre="">optional</code> 指定 path 前面要显示的秒数。</td>
</tr>
<tr>
<td>trailTime</td>
<td>Number</td>
<td></td>
<td><code v-pre="">optional</code> 指定 path 后面要显示的秒数。</td>
</tr>
<tr>
<td>width</td>
<td>Number</td>
<td><code v-pre="">1.0</code></td>
<td><code v-pre="">optional</code> 指定 path 像素宽度。</td>
</tr>
<tr>
<td>resolution</td>
<td>Number</td>
<td><code v-pre="">60</code></td>
<td><code v-pre="">optional</code> 指定 path 步进最大秒数。</td>
</tr>
<tr>
<td>material</td>
<td>Object|String|Array</td>
<td><code v-pre="">'WHITE'</code></td>
<td><code v-pre="">optional</code> 指定 path 材质。</td>
</tr>
<tr>
<td>distanceDisplayCondition</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> 指定 path 随相机距离改变是否显示参数。 <strong>结构：{ near: number, far: number }</strong></td>
</tr>
</tbody>
</table>
<hr>
<ul>
<li>参考官方文档： <strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/PathGraphics.html">PathGraphics</a></strong></li>
</ul>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例，以及当前组件的 cesiumObject。</td>
</tr>
<tr>
<td>definitionChanged</td>
<td></td>
<td>每当更改或修改属性或子属性时触发该事件。</td>
</tr>
</tbody>
</table>
